;AAEpB;;AAEA;IACI,kDAAkD;AACtD;;AAEA;IACI,wCAAwC;IACxC,gDAAgD;AACpD;;AAEA;;;;;GAKG","sourcesContent":["/* styles.css */\n/* From: Josh's Custom CSS Reset\n  https://www.joshwcomeau.com/css/custom-css-reset/\n*/\n*,\n*::before,\n*::after {\n    box-sizing: border-box;\n}\n\n* {\n    margin: 0;\n    padding: 0;\n}\n\nbody {\n    line-height: 1.5;\n    -webkit-font-smoothing: antialiased;\n}\n\nimg,\npicture,\nvideo,\ncanvas,\nsvg {\n    display: block;\n    max-width: 100%;\n}\n\ninput,\nbutton,\ntextarea,\nselect {\n    font: inherit;\n}\n\np,\nh1,\nh2,\nh3,\nh4,\nh5,\nh6 {\n    overflow-wrap: break-word;\n}\n\n#root,\n#__next {\n    isolation: isolate;\n}\n\n/* -------------------------------------------------------------------------\n*/\n:root {\n\n    font-size: 16px;\n\n    --sticky-note-font-size: 1rem;\n    --todoitems-grid-gap: 15px;\n    --sticky-note-width: 220px;\n    --sticky-note-height: 220px;\n\n\n    --expand-dots-button-size: 30px;\n    --sticky-note-flap-offset-height: 15px;\n    --sticky-note-flap-offset-width: 20px;\n\n    --small-button-padding: 4px;\n\n    --task-board-grid-width: calc(1 * var(--sticky-note-width));\n    --task-board-grid-height: var(--task-board-grid-width);\n\n    --main-block-border-radius: 30px;\n\n    --content-border-px: 2px;\n    --content-border-color: rgb(203, 195, 187);\n\n\n\n    font-family: \"Chakra Petch\", sans-serif;\n    font-weight: 400;\n    font-style: normal;\n\n    --body-height: 100vh;\n    --header-height: 200px;\n    --content-height: 750px;\n\n    --announcements-height: calc(0.5 * var(--content-height));\n    --trending-height: calc(var(--content-height) - var(--announcements-height));\n\n\n    --nav-header-size: 1.2rem;\n    --nav-header-padding: 19px;\n    --nav-li-size: 1.5rem;\n    --nav-list-margin-left: 30px;\n    --nav-margin-right: calc(var(--nav-list-margin-left) + 20px);\n\n    --nav-font-weight: 600;\n    --nav-li-seperator: 15px;\n\n    --nav-section-seperator: 40px;\n\n\n    /* All Colors */\n\n\n    --lighter-color: #f1f5f9;\n    --lighter-color-font-color: #292524;\n\n    --darker-color: #475569;\n    --darker-color-font-color: #e2e8f0;\n\n    --super-dark-bg-color: #0f172a;\n    --super-dark-color: #e2e8f0;\n\n    --save-submit-bg-color: #bef264;\n    --delete-cancel-bg-color: #ea580c;\n\n    --project-font-color: var(--super-dark-bg-color);\n\n\n    --header-section-bg-color: var(--lighter-color);\n    --header-section-color: var(--super-dark-bg-color);\n\n    --projects-bg-color: var(--darker-color);\n    --projects-button-bg-color: var(--lighter-color);\n    --projects-button-color: black;\n    --projects-nav-title-color: var(--lighter-color);\n\n    --tasks-header-bg-color: var(--darker-color);\n    --tasks-header-font-color: var(--darker-color-font-color);\n\n    --tasks-header-btn-bg-color: var(--lighter-color);\n    --tasks-header-btn-color: var(--darker-color);\n\n    --header-btn-bg-color: var(--darker-color);\n    --header-btn-color: var(--lighter-color);\n\n\n\n    --upcoming-bg-color: var(--super-dark-bg-color);\n    --upcoming-color: var(--super-dark-color);\n\n\n    /* --nav-section-bg-color: rgb(40, 142, 201);\n    --nav-color: #ffffff;\n\n    --main-page-font-color: #000000;\n    --search-bar-bg-color: #dae2e6;\n\n    --project-card-text-color: rgb(95, 95, 95);\n    --project-card-side-border-color: rgb(231, 196, 100);\n    --card-shadow-color: rgb(136, 136, 136); */\n\n    /* Green Color */\n\n    --header-left-right-margin: 40px;\n    --header-section-top-bottom-margin: 20px;\n\n\n    --header-top-height-percent: 35%;\n    --header-bottom-height-percent: calc(100% - var(--header-top-height-percent));\n\n    --border-radii: 10px;\n\n\n\n    --upper-right-buttons-width: calc(100% - var(--search-section-width));\n\n    --page-actions-buttons-font-size: 1.5rem;\n    --page-actions-buttons-font-weight: 700;\n    --page-actions-buttons-height: calc(2 * var(--page-actions-buttons-font-size));\n\n    --profile-name-font-size: 2rem;\n    -